$primary: var(--primary)
$secondary: var(--secondary)
$tertiary: var(--tertiary)

body.theme-stockman

	.page-header,.preface-header
		svg
			polygon
				fill: $primary

	.report-page-footer
		
		span
			color: $primary
	
	.cover-header
		
		.cover-title
			color: $primary
	
	.cover-image
		
		.item
			
			&.harken-blue
				background-color: $primary
			
			&.mid-blue
				background-color: $secondary
			
			&.dark-blue
				background-color: $tertiary
				
		.triangle
			
			&.harken-blue
				border-top-color: $primary
				
			&.mid-blue
				border-top-color: $secondary
			
			&.dark-blue
				border-top-color: $tertiary
	
	.cover-footer
		
		.cover-right
			
			.cover-user-name
				color: $primary
				
				i
					color: $primary


	
	.table-of-contents
		
		.index
			
			.index-item
				
				.number
					color: $primary
	
	.evaluation-engagement-letter
		
		.email-greeting
			color: $primary
			
		.letter-logo
			background-image: url('../../../../../assets/img/report/themes/logo-nai.png')
	
	.executive-summary
		
		.description
			
			.subtitle
				color: $primary
		
		.body
			
			.left
				
				.subtitle
					color: $primary
		.main-description_p
			span
				color: $primary !important
	
	.section-area
		
		.section-image
			background-color: $tertiary
		
	.property-summary
		
		.overview-body
			
			.title
				color: $primary
			
			.top-table-container
				
				.label-left-side
					color: $primary
					
			.bottom-title
			
				.label-left-side
					color: $primary

			.bottom-table-container

				.bottom-right-table-container
					border-left: 1px solid $primary
		
		.details-body
			
			.top-table
					
				.details-area
				
					.section-title
						color: $primary
					
					.footnote
						color: $primary
						
		.top-table
			
			.section-title
				color: $primary
	
	.section-title
		color: $primary
		
	.valuations_page_3 .space-table, .valuations_page_3 .comps-table, .type-table, .improvements-table, .improvements-table, .weighted-value table
		
		tbody
			
			tr
				
				td
					border-left-color: $primary
					
					&.has-border
						border-left-color: $primary
						
					&.has-bottom-border
						border-bottom-color: $primary
						
					&.has-underline
						text-decoration-color: $primary
					
					&.blue
						color: $primary
						background-color: transparent !important
          
	.comps-table
		
		tbody

			tr
				&:nth-child(1)

					td
						white-space: normal
		
	.valuations-page-1, .valuations-page-2, .valuations_page_3
	
		.description
		
			.subtitle
				color: $primary
				
		.regular-description

			span
				color: $primary

				&.text
					color: black

				
		table
			
			thead
				
				tr
					
					th
						color: $primary
						
			tbody
				
				.totals
					color: $primary
					
				tr
					
					td
						
						.background-text
							color: rgba($primary, 0.15)
							
	.comparable-number, .comparable-title
		color: $primary
		
	.business-properties
		
		.description, .left
			
			.subtitle
				color: $primary
				
		.main-description
			color: $primary

	#print-bov
		.btn
			background-color:  $primary

			&:hover
				background-color: $primary



.description
	table
		tr
			.amount-col
				width: 120px
			.amount-gross-col
				width: 125px
			.type-col
				width: 300px
			.amount-sf-col
				width: 100px
			td
				.text-col
					display: inline-block
					width: 165px
					text-align: right
					padding-right: 10px

					&:first-letter
						float: left
			&:nth-last-child(2)
				td
					.text-col
						border-bottom: 1px solid

				.gross-border
					text-decoration: none
					span
						border-bottom: 1px solid
		
			


.property-summary
	.fixed-action-btn
		.btn-floating
			img
				width: 100%



#map-landmarks
	margin-top: 10px
	padding: 10px
	display: flex
	border: 0.5px solid #BDBDBD
	.label
		margin-right: 2px
		font-weight: bold
		padding-top: 4px

	.map-landmark
		display: flex
		width: 100%
		.el
			width: 33.33%
			display: inline-block
			border-right: 1px solid #e7e7e7
			padding-right: 5px
			padding-left: 5px
		.list
			width: 100%
			display: flex
			font-size: 12px
			line-height: 26px
			align-items: flex-start
			strong
				white-space: nowrap
			.ellip
				margin-left: 5px
				word-break: break-word
			.img
				max-width: 35px
				text-align: center
				display: inline-flex
				padding: 4px 2px
				margin-right: 2px
				img
					width: auto
					max-height: 18px

			&.subject-text
				.img
					width: 23px
					margin-left: -3px
					margin-right: 0
