$primary: var(--primary)
$secondary: var(--secondary)
$tertiary: var(--tertiary)
$coverfontfamily: 'Centennial LT W05_75 Bold'
$fontfamily: 'Helvetica Neue LT W05 45 Light'
$sectionff: 'Centennial LT W05_56 Black'
$titleBoldff: 'Helvetica Neue LT W05 55 Roman'
$hff: 'Centennial LT W05_55 Roman'
$desff: 'Helvetica Neue LT W05 75 Bold'
$footnoteff: 'Helvetica Neue LT W05 46 Lt It'
$thff: 'Centennial LT W05_45 Light'

body.theme-nai

	.page-header,.preface-header
		svg
			polygon
				fill: $primary

	.report-page-footer

		span
			color: $primary

	.cover-header

		.cover-title
			color: $primary

	.cover-image

		.item

			&.harken-blue
				background-color: $primary

			&.mid-blue
				background-color: $secondary

			&.dark-blue
				background-color: $tertiary

		.triangle

			&.harken-blue
				border-top-color: $primary

			&.mid-blue
				border-top-color: $secondary

			&.dark-blue
				border-top-color: $tertiary

	.cover-footer

		.cover-right

			.cover-user-name
				color: $primary

				i
					color: $primary

	.table-of-contents

		.index

			.index-item

				.number
					color: $primary

	.evaluation-engagement-letter

		.email-greeting
			color: $primary

		.letter-logo
			background-image: url('../../../../../assets/img/report/themes/logo-nai.png')

	.executive-summary

		.description

			.subtitle
				color: $primary

		.body

			.left

				.subtitle
					color: $primary

	.section-area

		.section-image
			background-color: $tertiary

	.property-summary

		.overview-body

			.title
				color: $primary

			.top-table-container

				.label-left-side
					color: $primary

			.bottom-title

				.label-left-side
					color: $primary

			.bottom-table-container

				.bottom-right-table-container
					border-left: 1px solid $primary

		.details-body

			.top-table

				.details-area

					.section-title
						color: $primary

					.footnote
						color: $primary

		.top-table

			.section-title
				color: $primary

	.section-title
		color: white

	.valuations_page_3 .space-table, .valuations_page_3 .comps-table, .type-table, .improvements-table, .improvements-table, .weighted-value table

		tbody

			tr

				td
					border-left-color: $primary

					&.has-border
						border-left-color: $primary

					&.has-bottom-border
						border-bottom-color: $primary

					&.has-underline
						text-decoration-color: $primary

					&.blue
						color: $primary
						background-color: transparent !important

	.comps-table

		tbody

			tr

				&:nth-child(1)

					td
						white-space: normal

	.valuations-page-1, .valuations-page-2, .valuations_page_3

		.description

			.subtitle
				color: $primary

		.regular-description

			span
				color: $primary

				&.text
					color: black

		table

			thead

				tr

					th
						color: $primary

			tbody

				.totals
					color: $primary

				tr

					td

						.background-text
							color: rgba($primary, 0.15)

	.comparable-number, .comparable-title
		color: $primary

	.business-properties

		.description, .left

			.subtitle
				color: $primary

		.main-description
			color: $primary

	#print-bov
		.btn
			background-color: $primary

			&:hover
				background-color: $primary

	font-family: $fontfamily

body.theme-nai
	p, a
		font-family: $fontfamily
	h1, h2, h3, h4, h5, h6
		font-family: $hff
	.preface-header
		.section-name
			font-family: $sectionff
	.page-header
		.section-name
			font-family: $desff
		.section-title
			font-family: $sectionff
	.h1
		font-family: $hff
	.cover-header
		.cover-title
			font-family: $coverfontfamily
	.cover-footer
		.cover-right
			.cover-user-name
				font-family: $desff

			i
				font-family: $footnoteff

		.cover-business-name
			font-family: $desff
		.cover-email
			font-family: $fontfamily
	.table-of-contents
		.index
			.index-item
				.index-number
					font-family: $desff
			.number
				font-family: $hff
			.index-text
				.section-text-one
					font-family: $desff
	.evaluation-engagement-letter
		.email-header
			font-family: $desff
	.executive-summary
		.body
			.title
				font-family: $titleBoldff
			.left
				.subtitle
					font-family: $hff
				.item
					font-family: $desff
				.item-description
					font-family: $fontfamily
				.key-highlights
					font-family: $titleBoldff
				.highlights-list
					p
						font-family: $titleBoldff
				.highlight-item
					font-family: $fontfamily
			.bottom
				.summary-content
					font-family: $titleBoldff
		.description
			.title
				font-family: $titleBoldff
	.valuations-page-1 .description .title
		font-family: $titleBoldff
	.executive-summary .description .subtitle, .valuations-page-1 .description .subtitle
		font-family: $hff
	.executive-summary .description
		.main-description
			font-family: $desff
		.extra-description
			font-family: $fontfamily
		.main-description_p
			font-family: $fontfamily !important
			span
				font-family: $hff !important
				font-size: 16px !important
				color: $primary !important
	.property-summary
		.overview-body
			.title
				font-family: $hff
			.top-table-container
				.label-left-side
					font-family: $hff
			.content-left-side
				font-family: $fontfamily
			.label-right-side
				font-family: $fontfamily
			.content-right-side
				font-family: $fontfamily
			.bottom-title
				.label-left-side
					font-family: $hff
			.bottom-table-container
				.bottom-left-first-table-container
					.label-bottom
						font-family: $desff
				.bottom-left-second-table-container .label-bottom, .bottom-right-table-container .label-bottom
					font-family: $desff
				.bottom-left-first-table-container .value-bottom, .bottom-left-second-table-container .value-bottom, .bottom-right-table-container .value-bottom
					font-family: $fontfamily
		.description-left
			.property-zoning-title
				font-family: $desff
			.property-zoning-description
				font-family: $fontfamily
	.section-title
		font-family: $sectionff
	.property-description
		.label-right-side
			font-family: $fontfamily
		.content-right-side
			font-family: $fontfamily
	.property-description-right
		.label-right-side
			font-family: $fontfamily
		.content-right-side
			font-family: $fontfamily
	.property-summary
		.top-table
			.info-title
				font-family: $titleBoldff
			.section-title
				font-family: $hff
			.area-info
				font-family: $fontfamily
		.details-body
			.top-table
				.details-area
					.section-title
						font-family: $hff
					.section-subtitle
						font-family: $fontfamily
					.property-label
						font-family: $fontfamily
					.property-value
						font-family: $desff
					.footnote
						font-family: $footnoteff
	.valuations-page-1
		.description
			.title
				font-family: $titleBoldff
			.subtitle
				font-family: $hff
		.regular-description span
			font-family: $hff
	.valuations-page-2
		.description
			.title
				font-family: $titleBoldff
			.subtitle
				font-family: $hff
		.regular-description
			span
				font-family: $hff
	.valuations_page_3
		.description
			.title
				font-family: $titleBoldff
			.subtitle
				font-family: $hff

		.regular-description
			span
				font-family: $hff

				&.text
					font-family: $fontfamily
		table
			thead tr th
				font-family: $hff
			tbody
				tr
					td:first-of-type
						font-size: 14px
						text-align: left
					td
						font-size: 14px
						.blue
							font-family: $sectionff

		.comps-table
			tbody
				tr
					td
						&:first-of-type
							border-left: none
						&.bold
							font-family: $desff !important
							&.mont-bold
								font-family: $desff,serif !important
						&.blue
							font-family: $sectionff

	.weighted-value
		table
			thead
				tr
					th
						font-family: $thff
			// tbody
			// 	tr
			// 		.blue
			// 			font-family: $sectionff

	.business-properties
		.description
			.title
				font-family: $hff
		.left .title
			font-family: $hff
		.description .subtitle, .left .subtitle
			font-family: $titleBoldff
		.main-description
			font-family: $hff
		.single-description
			font-family: $hff
		.regular-description
			span
				font-family: $hff
		.description-right
			.property-zoning-title
				font-family: $hff
			.property-zoning-description
				font-family: $fontfamily
		table
			thead tr th
				font-family: $thff
	.cap-rate-table
		.net-operating-row
			b
				font-family: $desff
	.new-page
		&.pdf-png-row
			.description
				.regular-description
					strong
						font-family: $desff
	span.red-stylish-dec
		font-family: $hff
		color: $primary !important
		font-family: $hff
	.property-summary .description-left .ft-size-20
		font-family: $fontfamily
	.red-styl-font
		color: $primary !important
		font-size: 20px
		font-family: $hff
		margin-top: 18px
		font-family: $hff
	.property-summary .description-left .ft-size-20
		font-family: $titleBoldff
	.comps-modal-table
		tbody
			tr
				td
					font-size: 12px
					border-left: 2px solid #0da1c7

					&:first-of-type
						border-left: none
					&.bold
						font-weight: bold
						font-family: $desff !important
						&.mont-bold
							font-family: $desff !important
					&.blue
						font-family: $sectionff

.description
	table
		tr
			.amount-col
				width: 120px
			.amount-gross-col
				width: 125px
			.type-col
				width: 300px
			.amount-sf-col
				width: 100px
			td
				.text-col
					display: inline-block
					width: 165px
					text-align: right
					padding-right: 10px

					&:first-letter
						float: left
			&:nth-last-child(2)
				td
					.text-col
						border-bottom: 1px solid

				.gross-border
					text-decoration: none
					span
						border-bottom: 1px solid

.property-summary
	.fixed-action-btn
		.btn-floating
			img
				width: 100%

#map-landmarks
	margin-top: 10px
	padding: 10px
	display: flex
	border: 0.5px solid #BDBDBD
	.label
		margin-right: 2px
		font-weight: bold
		padding-top: 4px

	.map-landmark
		display: flex
		width: 100%
		.el
			width: 33.33%
			display: inline-block
			border-right: 1px solid #e7e7e7
			padding-right: 5px
			padding-left: 5px
		.list
			width: 100%
			display: flex
			font-size: 12px
			line-height: 26px
			align-items: flex-start
			strong
				white-space: nowrap
			.ellip
				margin-left: 5px
				word-break: break-word
			.img
				max-width: 35px
				text-align: center
				display: inline-flex
				padding: 4px 2px
				margin-right: 2px
				img
					width: auto
					max-height: 18px

			&.subject-text
				.img
					width: 23px
					margin-left: -3px
					margin-right: 0
