@page
  size: letter landscape

$primary: var(--primary)
$secondary: var(--secondary)
$tertiary: var(--tertiary)

@media print
  body, .new-page[size="letter"]
    margin: 0
    padding: 0
    box-shadow: none

body
  counter-reset: page
  font-family: 'Montserrat Regular', sans-serif
  color: #1C3643

p, a
  font-family: 'Montserrat Regular', sans-serif
  color: #1C3643

h1, h2, h3, h4, h5, h6
  font-family: 'Roboto Slab Regular', serif
  color: $primary

table
  border-collapse: unset !important
  td
    padding: 0 !important
#evaluation-report-data
  width: 279mm
  margin: 0 auto
  
.new-page
  counter-increment: page
  position: relative
  background: white
  width: 279mm
  height: 215mm
  display: block
  margin: 0 auto
  box-shadow: 0 0 1cm rgba(0, 0, 0, 0.1)
  page-break-after: always
  margin-bottom: 10px
  padding: 10px

  .file-number
    float: right
    margin: 20px

.preface-header
  float: right
  width: 290px
  height: 55px
  position: relative

  svg
    polygon
      fill: $primary

  .section-name
    font-family: 'Roboto Slab Light', serif
    font-size: 24px
    margin-top: 10px
    vertical-align: middle
    color: #FFFFFF
    padding-left: 40px

.page-header
  top: 0
  right: 0
  width: 290px
  height: 55px
  float: right
  position: relative

  svg
    polygon
      fill: $primary

  .section-name
    font-family: 'Montserrat SemiBold', sans-serif
    color: #FFFFFF
    font-size: 14px
    padding-left: 50px
    margin: 2px 0 -28px 0

  .section-title
    font-family: 'Roboto Slab Light', serif
    color: #FFFFFF
    font-size: 20px
    margin-top: 30px
    padding-left: 50px

.report-page-footer
  font-size: 13px
  position: absolute
  bottom: 5px
  right: 15px

  span
    color: $primary
    font-weight: bold
  .pagenum:before
    content: counter(page)
  .pagenum.without-before-element:before
    content: none

.section-area
  width: 20%
  height: 100%
  float: left
  position: relative
  text-align: center
  color: white

  .section-image
    width: 100%
    height: 100%
    background-color: $tertiary

  div
    position: absolute
    top: 18%
    right: -10%
    transform: rotate(90deg)
    font-size: 60px
    font-weight: bold
    width: 400px

.h1
  font-family: 'Roboto Slab Regular', serif
  color: $primary
  font-size: 52px

.clear
  clear: both

.image-added
  object-fit: cover !important

