$primary: var(--primary)
$secondary: var(--secondary)
$tertiary: var(--tertiary)
$coverfontfamily: 'Centennial LT W05_75 Bold'
$fontfamily: 'Helvetica Neue LT W05 45 Light'
$sectionff: 'Centennial LT W05_56 Black'
$titleBoldff: 'Helvetica Neue LT W05 55 Roman'
$hff: 'Centennial LT W05_55 Roman'
$desff: 'Helvetica Neue LT W05 75 Bold'
$footnoteff: 'Helvetica Neue LT W05 46 Lt It'
$thff: 'Centennial LT W05_45 Light'

body.theme-nai_portland

	// color: $primary !important

	.page-header,.preface-header
		svg
			polygon
				fill: $primary

	.report-page-footer
		
		span
			color: $primary
	
	.cover-header
		
		.cover-title
			color: $primary
	
	.cover-image
		
		.item
			
			&.harken-blue
				background-color: $primary
			
			&.mid-blue
				background-color: $secondary
			
			&.dark-blue
				background-color: $tertiary
				
		.triangle
			
			&.harken-blue
				border-top-color: $primary
				
			&.mid-blue
				border-top-color: $secondary
			
			&.dark-blue
				border-top-color: $tertiary
	
	.cover-footer
		
		.cover-right
			
			// .cover-business-name
			// 	color: $primary

			.cover-user-name
				color: $primary
				
				i
					color: $primary
	
	.table-of-contents
		
		.index
			
			.index-item
				
				.number
					color: $primary
	
	.evaluation-engagement-letter
		
		.email-greeting
			color: $primary
			
		.letter-logo
			background-image: url('../../../../../assets/img/report/themes/logo-nai.png')
	
	.executive-summary
		
		.description
			
			.subtitle
				color: $primary
		
		.body
			
			.left
				
				// .item
				// 	color: $primary

				.subtitle
					color: $primary
	
	.section-area
		
		.section-image
			background-color: $tertiary
		
	.property-summary
		.overview-body
			.title
				color: $primary
			.top-table-container
				.label-left-side
					color: $primary
			.bottom-title
				.label-left-side
					color: $primary
			.bottom-table-container
				.bottom-right-table-container
					border-left: 1px solid $primary
					
					// .label-bottom
					// 	color: $primary
				
				// .bottom-left-first-table-container 
					
				// 	.label-bottom
				// 		color: $primary
		
				// .bottom-left-second-table-container 
					
				// 	.label-bottom
				// 		color: $primary
		
		.details-body
			
			.top-table
					
				.details-area
				
					.section-title
						color: $primary
					
					.footnote
						color: $primary
						
		.top-table
			
			.section-title
				color: $primary
	
	.section-title
		color: $primary
		
	.valuations_page_3 .space-table, .valuations_page_3 .comps-table, .type-table, .improvements-table, .improvements-table, .weighted-value table
		
		tbody
			
			tr
				
				td
					border-left-color: $primary
					
					&.has-border
						border-left-color: $primary
						
					&.has-bottom-border
						border-bottom-color: $primary
						
					&.has-underline
						text-decoration-color: $primary
					
					&.blue
						color: $primary
						background-color: transparent !important
          
	.comps-table
		
		tbody

			tr
				&:nth-child(1)

					td
						white-space: normal
		
	.valuations-page-1, .valuations-page-2, .valuations_page_3
	
		.description
		
			.subtitle
				color: $primary
				
		.regular-description

			span
				color: $primary

				&.text
					color: black

				
		table
			
			thead
				
				tr
					
					th
						color: $primary
						
			tbody
				
				.totals
					color: $primary
					
				tr
					
					td
						
						.background-text
							color: rgba($primary, 0.15)
							
	.comparable-number, .comparable-title
		color: $primary
		
	.business-properties
		
		.description, .left
			
			.subtitle
				color: $primary
				
		.main-description
			color: $primary

	#print-bov
		.btn
			background-color:  $primary

			&:hover
				background-color: $primary

	font-family: $fontfamily

