			tr
				td
					font-size: 12px
					border-left: 2px solid #0da1c7

					&:first-of-type
						border-left: none
					&.bold
						font-weight: bold
						font-family: $desff !important
						&.mont-bold
							font-family: $desff !important
					&.blue
						font-family: $sectionff


.description
	table
		tr
			.amount-col
				width: 120px
			.amount-gross-col
				width: 125px
			.type-col
				width: 300px
			.amount-sf-col
				width: 100px
			td
				.text-col
					display: inline-block
					width: 165px
					text-align: right
					padding-right: 10px

					&:first-letter
						float: left
			&:nth-last-child(2)
				td
					.text-col
						border-bottom: 1px solid

				.gross-border
					text-decoration: none
					span
						border-bottom: 1px solid

.property-summary
	.fixed-action-btn
		.btn-floating
			img
				width: 100%

#map-landmarks
	margin-top: 10px
	padding: 10px
	display: flex
	border: 0.5px solid #BDBDBD
	.label
		margin-right: 2px
		font-weight: bold
		padding-top: 4px

	.map-landmark
		display: flex
		width: 100%
		.el
			width: 33.33%
			display: inline-block
			border-right: 1px solid #e7e7e7
			padding-right: 5px
			padding-left: 5px
		.list
			width: 100%
			display: flex
			font-size: 12px
			line-height: 26px
			align-items: flex-start
			strong
				white-space: nowrap
			.ellip
				margin-left: 5px
				word-break: break-word
			.img
				max-width: 35px
				text-align: center
				display: inline-flex
				padding: 4px 2px
				margin-right: 2px
				img
					width: auto
					max-height: 18px

			&.subject-text
				.img
					width: 23px
					margin-left: -3px
					margin-right: 0
